.group{
    margin-bottom: 8px;
}
.header-above{
    font-size: 20px;
    font-weight: 500;
}
.header{
    border: blueviolet 2px dashed;
    padding: 10px 5px;
}
.title{
    font-size: 58px;
    text-align: center;
    color:brown;
}
.questions-count{
    width: 35px;
    color: red;
    font-weight: 600;
}
.font-size18{
    font-size: 18px;
}
.question{
    font-size: 18px;
}
.red{
    color: red;
    font-weight: 600;
}
.button-groups{
    margin-top: 10px;
}
.nodata-message{
    color: blue;
    font-size: 23px;
}
.blue{
    color: blue;
}
.main-area{
    height: 700px;
    overflow: auto;
}
.answer-boder{
    border-style: double;
    border-color: blue;
}
.container{
    margin-bottom: 15px;
}
/* 答案输入框 */
.answer-input{
    text-align: center;
    width: 80px;
    padding: 6px 12px;
}
.answer-input:focus{
    border:2px solid red;
    background-color: yellow;
}
/* 计时 */
.spend-time{
    font-size: 18px;
    color: blue;
}